.searchMovie {
  display: block;
//   padding: 0 20px;
  max-width: 940px;
  margin: auto;
  height: 80px;
  position: absolute;
  top: 0;
  width: 100%;
  left: 50%;
  transform: translate(-50%, -50%);

  form {
    height: 80px;
    background-color: #fff;
    border-radius: 5px;
    z-index: 2;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
    ::after {
      display: none;
    }
    ::before {
      content: "";
      position: absolute;
      left: 0;
      height: 50%;
      top: 50%;
      transform: translateY(-50%);
      border-right: 1px solid #fff;
      border-right-color: rgba(238, 238, 238, 0.88);
    }
    .w30p {
      width: 30%;
    }
    .smallBlock {
      width: calc(70% / 4);
    }
    .widthByPercent {
      float: left;
      position: relative;
      height: 100%;
      ::before {
        display: none;
      }
      .selectMenu {
        text-align: left;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        left: 0;
        background-color: transparent;
        border: none;
        color: #000;
        cursor: pointer;
        width: 100%;
        padding: 7%;
        // background-image: url("../../img/dropdown-icon.png");
        // background-repeat: no-repeat;
        // background-position: calc(100% - 8px);
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        padding-right: 20px;
        &:focus {
          outline: none;
          box-shadow: none;
        }
      }
      #buy_tiket {
        background: linear-gradient(to left, #fb4226, #ce3017 100%);
        border: none;
        text-transform: uppercase;
        border-radius: 4px;
        position: absolute;
        top: 50%;
        left: 10%;
        transform: translate(0, -50%);
        padding: 10px 15px;
        color: white;
        &:focus {
          // outline: none;
          box-shadow: none;
        }
      }
    }
  }
  @include responsive("medium") {
    display: none !important;
}
}
