.login {
    position: absolute;
    max-width: 360px;
    padding: 40px 32px 30px;
    background-image: linear-gradient(to bottom,rgba(20,50,93,.9),rgba(8,22,48,.9));
    box-shadow: 0 0 10px 0 rgba(0,0,0,.45);
    text-align: center;
    color: #fff;
    border-radius: 6px;
    h3 {
        color: #fff;
    }
    .form-group {
        text-align: left;
    }
    .form-group.btnSubmit {
        text-align: right;

    }
}