@import '../../assets/scss/Helper/variable';
@import '../../assets/scss/Helper/mixin';

.showTimeTemplate {
  height: 100vh;
  .ant-layout-header {
    width: calc(100% - 320px) !important;
    position: fixed;
    top: -1px;
    left: 0;
    width: 100%;
    height: 60px !important;
    padding: 0 !important;
    background-color: rgba(255, 255, 255, 0.95) !important;
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.3);
    z-index: 9;
    transition: all 0.2s;
    .navbar {
      height: 60px !important;
      padding: 0 !important;
      display: flex;
      justify-content: space-between;
      .header__left,
      .header__right {
        height: 60px !important;
        display: flex;
        align-items: center;
      }
      .header__left {
        justify-content: left;
        padding-left: 25px;
        .navbar-brand {
          display: flex;
          align-items: center;
          padding: 0;
          height: 60px !important;
          img {
            width: 45px;
            height: 45px;
            vertical-align: middle;
          }
        }
      }
    }
  }
  .ant-layout {
    background: none !important;
  }

  .ant-layout-content {
    width: calc(100% - 320px) !important;
    margin-top: 60px;
    overflow-x: hidden;
  }
  @include responsive("large") {
    height: auto;
    .ant-layout-header, .ant-layout-content {
      width: 100% !important;
      background: #fff;
    }
  }
}
